@use '../../foundation/_variable' as *;

.p-cta {
    padding-top: 6rem;
    background: linear-gradient($color-bg-cream, transparent 20%);
    @include breakpoint(small) {
    padding-top: 2rem;
    }
    &__bgBase {
        background: $color-base;
    }
    &__bgYellow {
        background: linear-gradient($color-bg-yellow,  transparent 20%);
    }
    &__heading img {
        margin: 0 auto;
        width: min(27rem, 42vw);
        height: auto;
    }
    &__btn {
        margin: 2rem auto 0.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        &::after {
            content: "";
            display: block;
            width: 3.4rem;
            height: 2.8rem;
            background: url("../../assets/img/btn-arrow.svg") center / contain no-repeat;
            margin-left: 2.4rem;
        }
        @include breakpoint(small) {
            justify-content: center;
            &::after {
                margin-left: 4vw;
                width: 5.6vw;
            }
        }
    }
    &__img {
        margin-top: 5.5rem;
        @include breakpoint(small) {
            margin-top: 3rem;
        }
        & img {
            width: 100%;
            object-fit: contain;
            object-position: bottom;
        }
    }
    &__img__short {
        margin-top: 0;
    }
    &.u-pt__40 {
        padding-top: 4rem;
        @include breakpoint(small) {
            padding-top: 2rem;
        }
    }
}
